export enum MenuOrientation {
  STATIC,
  OVERLAY,
  SLIM,
  HORIZONTAL
}

export interface Menu {
  orientation?: MenuOrientation;
  dark?: boolean;
  menuItems?: MenuItem[];
}

export interface MenuItem {
  id?: string;
  name?: string;
  label?: string; // 	null	Text of the item.
  title?: string; // null, non clickable item text
  tag?: string; //
  icon?: string; // 	null	Icon of the item.
  command?: (event?: any) => void; // 	null	Callback to execute when item is clicked.
  url?: string; // 	null	External link to navigate when item is clicked.
  routerLink?: any[] | string; // 	null	RouterLink definition for internal navigation.
  routerLinkActiveOptions?: {}; // 	null	Configuration for active router link.
  queryParams?: {
    [s: string]: any;
  }; // 	null	Query parameters for internal navigation via routerLink.
  fragment?: string; // null	bound to RouterLink.fragmentï¼as # in url
  items?: MenuItem[]; // null	An array of children menuitems.
  expanded?: boolean; // 	false	Visibility of submenu.
  disabled?: boolean; // 	false	When set as true, disables the menuitem.
  visible?: boolean; // 	true	Whether the dom element of menuitem is created or not.
  target?: string; // 	null	Specifies where to open the linked document.
  separator?: boolean; // 	false	Defines the item as a separator.
  style?: {}; // null	Inline style of the menuitem.
  styleClass?: string; // 	null	Style class of the menuitem.
  badge?: string; // 	null	Value of the badge.
  badgeStyleClass?: string; // 	null	Style class of the badge
  badgeTheme?: string; // 	null	Style class of the badge
  automationId?: any;
  roles?: string | string[];
  permissions?: {
    only?: string | string[] | ((context) => string | string[]);
    except?: string | string[]
  };
}
