import {UiPosition} from '../position';

export interface NotifyProp {
  type?: NotifyType;
  key?: string;
  level?: NotifyLevel;
  animation?: boolean;
  title?: string;
  icon?: string;
  message?: string;
  style?: any;
  styleClass?: any;
  sticky?: boolean;
  duration?: number;
  modal?: boolean;
  position?: UiPosition;
  immutable?: boolean;
  closable?: boolean;
  baseZIndex?: number;
  autoZIndex?: boolean;
  data?: any;

  [key: string]: any;
}

export enum NotifyType {
  ALERT = 'alert',
  ANNOUNCE = 'announce',
}

export enum NotifyLevel {
  INFO = 'info',
  ERROR = 'error',
  SUCCESS = 'success',
  WARN = 'warn',
  QUESTION = 'question'
}

export enum NotifyMethod {
  APPEND = 'append',
  REPLACE = 'replace'
}
