import {EventEmitter} from '@angular/core';
import {ButtonProps} from '../button';
import {DynaContent} from '../dyna-content';


export enum DialogType {
  CONFIRM = 'confirm',
  POPUP = 'popup'
}

export interface DialogProp {
  header?: string;
  width?: number;
  height?: number;
  closeOnEscape?: boolean;
  context?: any;
}

export interface ConfirmDialogProp extends DialogProp {
  content?: string;
  icon?: string; // null	Icon to display next to message.
  key?: string; // null	Optional key to match the key of the confirm dialog, necessary to use when component tree has multiple confirm dialogs.
  rtl?: boolean; // false	When enabled dialog is displayed in RTL direction.
  closable?: boolean; // true	Adds a close icon to the header to hide the dialog.
  responsive?: boolean; // true	In responsive mode, dialog adjusts itself to screen width.
  appendTo?: any; // null	Target element to attach the dialog, valid values are "body" or a local ng-template variable of another element.
  acceptLabel?: string; // Yes	Label of the accept button.
  acceptIcon?: string; // fa-check	Icon of the accept button.
  acceptVisible?: boolean; // true	Visibility of the accept button.
  rejectLabel?: string; // No	Label of the reject button.
  rejectIcon?: string; // fa-close	Icon of the reject button.
  rejectVisible?: boolean; // true	Visibility of the reject button.
  acceptButtonStyleClass?: string; // null	Style class of the accept button.
  rejectButtonStyleClass?: string; // null	Style class of the reject button.
  onAccept?: (context) => void; // Callback to execute when action is confirmed.
  onReject?: (context) => void; // Callback to execute when action is rejected.
  acceptEvent?: EventEmitter<any>;
  rejectEvent?: EventEmitter<any>;
}

export interface PopupDialogProp extends DialogProp {
  content?: DynaContent;
  header?: string;
  draggable?: boolean; // true	Enables dragging to change the position using header.
  resizable?: boolean; // true	Enables resizing of the content.
  minWidth?: number; // 150	Minimum width of a resizable dialog.
  minHeight?: number; // 150	Minimum width of a resizable dialog.
  contentMaxHeight?: number;
  contentMaxWidth?: number;
  contentWidth?: number;
  contentHeight?: number;
  contentStyle?: {}; // null	Style of the content section.
  visible?: boolean; // false	Specifies the visibility of the dialog.
  modal?: boolean; // false	Defines if background should be blocked when dialog is displayed.
  dismissableMask?: boolean; // false	Specifices if clicking the modal background should hide the dialog.
  rtl?: boolean; // false	When enabled dialog is displayed in RTL direction.
  closable?: boolean; // true	Adds a close icon to the header to hide the dialog.
  responsive?: boolean; // true	In responsive mode, dialog adjusts itself to screen width.
  breakpoint?: number; // 640	Maximum screen width to display the dialog with 100% width in responsive mode.
  appendTo?: any; // null	Target element to attach the dialog, valid values are "body" or a local ng-template variable of another element.
  style?: any; // null	Inline style of the component.
  styleClass?: any;	// null	Style class of the component.
  showHeader?: boolean; // true	Whether to show the header or not.
  showFooter?: boolean; // true	Whether to show the footer or not.
  positionLeft?: number; // null	Left coordinate value of the dialog.
  positionTop?: number; // null	Top coordinate value of the dialog.
  baseZIndex?: number; // 0	Base zIndex value to use in layering.
  autoZIndex?: boolean; // true	Whether to automatically manage layering.
  ariaLabelledBy?: string; // null
  blockScroll?: boolean;
  maximizable?: boolean;
  closeLabel?: string;
  closeIcon?: string;
  onClose?: (context) => boolean;
  buttons?: ButtonProps[];
}



