import {ApiDataProps} from '../api-data/index';
import {DataItemProps} from '../data-item';
import {DataSource} from '../data-source/index';
import {Props} from '../props';

export enum ChartType {
  PIE = 'pie',
  LINE = 'line',
  BAR = 'bar',
  RADAR = 'radar',
  AREA = 'polarArea',
  DOUGHNUT = 'doughnut'
}

export interface ChartDimension {
  title?: string;
  dataSrcPath?: string;
  dataItemProps?: DataItemProps;
}

export interface ChartProps extends Props {
  type?: ChartType;
  data?: ChartDataProps;
  rawData?: {}[];
  options?: {};
  caption?: string;
  captionIcon?: string;
  width?: string;
  height?: string;
  switchable?: boolean;
  dataSource?: DataSource | ApiDataProps | ApiDataProps[];
  dimensions?: ChartDimension | ChartDimension[] | ((rawData) => ChartDataProps);
  dataItemProps?: DataItemProps;
  resolveDataSource?: { method: ((ds: DataSource) => DataSource) };
  resolveDimension?: { method: ((dimension: ChartDimension) => ChartDimension) };

  [key: string]: any;
}

export interface ChartDataProps {
  labels?: string[];
  datasets?: ChartDataSetsProps | ChartDataSetsProps[];
}

export interface ChartDataSetsProps {
  label?: string;
  data?: number[];

  [key: string]: any;
}

export interface LineChartOptions {
  showLines?: boolean; // true	If false, the lines between points are not drawn.
  spanGaps?: boolean; // false	If false, NaN data causes a break in the line.
}

export interface LineChartDataSetProps extends ChartDataSetsProps {
  xAxisID?: string; // The ID of the x axis to plot this dataset on. If not specified, this defaults to the ID of the first found x axis
  yAxisID?: string; // The ID of the y axis to plot this dataset on. If not specified, this defaults to the ID of the first found y axis.
  backgroundColor?: string; // The fill color under the line. See strings
  borderColor?: string; // 	The color of the line. See strings
  borderWidth?: number; // 	The width of the line in pixels.
  borderDash?: number[]; // 	Length and spacing of dashes. See MDN
  borderDashOffset?: number; // 	Offset for line dashes. See MDN
  borderCapStyle?: string; // 	Cap style of the line. See MDN
  borderJoinStyle?: string; // 	Line joint style. See MDN
  cubicInterpolationMode?: string; // Algorithm used to interpolate a smooth curve from the discrete data points. more...
  fill?: boolean | string; // 	How to fill the area under the line. See area charts
  lineTension?: number; // 	Bezier curve tension of the line. Set to 0 to draw straightlines. This option is ignored if monotone cubic interpolation is used.
  pointBackgroundColor?: string | string[]; // The fill color for points.
  pointBorderColor?: string | string[]; // 	The border color for points.
  pointBorderWidth?: number | number[]; // 	The width of the point border in pixels.
  pointRadius?: number | number[]; // 	The radius of the point shape. If set to 0, the point is not rendered.
  pointStyle?: string | string[]; // 	Style of the point. more...
  pointRotation?: number | number[]; // 	The rotation of the point in degrees.
  pointHitRadius?: number | number[]; // 	The pixel size of the non-displayed point that reacts to mouse events.
  pointHoverBackgroundColor?: string | string[]; // 	Point background color when hovered.
  pointHoverBorderColor?: string | string[]; // 	Point border color when hovered.
  pointHoverBorderWidth?: number | number[]; // 	Border width of point when hovered.
  pointHoverRadius?: number | number[]; // 	The radius of the point when hovered.
  showLine?: boolean; // 	If false, the line is not drawn for this dataset.
  spanGaps?: boolean; // 	If true, lines will be drawn between points with no or null data. If false, points with NaN data will create a break in the line
  steppedLine?: boolean; /// string	If the line is shown as a stepped line. more...
}

export interface BarChartOptions {
  barPercentage?: number; // 	0.9	Percent (0-1) of the available width each bar should be within the category width. 1.0 will take the whole category width and put the bars right next to each other. more...
  categoryPercentage?: number; // 	0.8	Percent (0-1) of the available width each category should be within the sample width. more...
  barThickness?: number | string; // 		Manually set width of each bar in pixels. If set to 'flex', it computes "optimal" sample widths that globally arrange bars side by side. If not set (default), bars are equally sized based on the smallest interval. more...
  maxBarThickness?: number; // 		Set this to ensure that bars are not sized thicker than this.
}

export interface BarChartDataSetProps extends ChartDataSetsProps {
  xAxisID?: string; // 	The ID of the x axis to plot this dataset on. If not specified, this defaults to the ID of the first found x axis
  yAxisID?: string; // 	The ID of the y axis to plot this dataset on. If not specified, this defaults to the ID of the first found y axis.
  backgroundColor?: string | string[]; // 	The fill color of the bar. See strings
  borderColor?: string | string[]; // 	The color of the bar border. See strings
  borderWidth?: number | number[]; // 	The stroke width of the bar in pixels.
  borderSkipped?: string; // 	Which edge to skip drawing the border for. more...
  hoverBackgroundColo?: string | string[]; // 	The fill colour of the bars when hovered.
  hoverBorderColor?: string | string[]; // 	The stroke colour of the bars when hovered.
  hoverBorderWidth?: number | number[]; // 	The stroke width of the bars when hovered.
}

export interface RadarChartDataSetProps extends ChartDataSetsProps {
  backgroundColor?: string; // 	The fill color under the line. See Colors
  borderColor?: string; // The color of the line. See Colors
  borderWidth?: number; // 	The width of the line in pixels.
  borderDash?: number[]; // 	Length and spacing of dashes. See MDN
  borderDashOffset?: number; // 	Offset for line dashes. See MDN
  borderCapStyle?: string; // 	Cap style of the line. See MDN
  borderJoinStyle?: string; // 	Line joint style. See MDN
  fill?: boolean | string; // 	How to fill the area under the line. See area charts
  lineTension?: number; // 	Bezier curve tension of the line. Set to 0 to draw straightlines.
  pointBackgroundColor?: string | string[]; // 	The fill color for points.
  pointBorderColor?: string | string[]; // 	The border color for points.
  pointBorderWidth?: number | number[]; // 	The width of the point border in pixels.
  pointRadius?: number | number[]; // 	The radius of the point shape. If set to 0, the point is not rendered.
  pointRotation?: number | number[]; // 	The rotation of the point in degrees.
  pointStyle?: 'circle' | 'cross' | 'crossRot' | 'dash' | 'line' | 'rect' | 'rectRounded' | 'rectRot' | 'star' | 'triangle';
  pointHitRadius?: number | number[]; // 	The pixel size of the non-displayed point that reacts to mouse events.
  pointHoverBackgroundColor?: string | string[]; // 	Point background color when hovered.
  pointHoverBorderColor?: string | string[]; // 	Point border color when hovered.
  pointHoverBorderWidth?: number | number[]; // 	Border width of point when hovered.
  pointHoverRadius?: number | number[]; // 	The radius of the point when hovered.
}

export interface PieChartOptions {
  cutoutPercentage?: number; // 	50 - for doughnut, 0 - for pie	The percentage of the chart that is cut out of the middle.
  rotation?: number; // 	-0.5 * Math.PI	Starting angle to draw arcs from.
  circumference?: number; // 	2 * Math.PI	Sweep to allow arcs to cover
}

export interface PieChartDataSetProps extends ChartDataSetsProps {
  backgroundColor?: string[]; // 	The fill color of the arcs in the dataset. See Colors
  borderColor?: string[]; // 	The border color of the arcs in the dataset. See Colors
  borderWidth?: number[]; // 	The border width of the arcs in the dataset.
  hoverBackgroundColor?: string[]; // 	The fill colour of the arcs when hovered.
  hoverBorderColor?: string[]; // 	The stroke colour of the arcs when hovered.
  hoverBorderWidth?: number[]; // 	The stroke width of the arcs when hovered.
}

export interface PolarAreaDataSetProps extends ChartDataSetsProps {
  backgroundColor?: string[]; // 	The fill color of the arcs in the dataset. See Colors
  borderColor?: string[]; // 	The border color of the arcs in the dataset. See Colors
  borderWidth?: number[]; // 	The border width of the arcs in the dataset.
  hoverBackgroundColor?: string[]; // The fill colour of the arcs when hovered.
  hoverBorderColor?: string[]; // 	The stroke colour of the arcs when hovered.
  hoverBorderWidth?: number[]; // 	The stroke width of the arcs when hovered.
}

export interface PolarAreaOptions {
  startAngle?: number; // -0.5 * Math.PI	Starting angle to draw arcs for the first item in a dataset.
}
