export const DEFAULT_QUERY_CONDITION = 'AND';

export interface QueryOperator {
  label?: string;
  key?: string;
  inputs?: number;
  multiple?: boolean;
  dataTypes?: string[];
  operator?: string;
  getValue?: (v) => any;
}

export interface QueryOperators {
  [key: string]: QueryOperator;
}

export interface Query {
  $and?: any[];
  $or?: any[];
  $not?: any[];

  [key: string]: any;
}

export interface QueryRule {
  entity?: string;
  field?: string;
  value?: any;
  operator?: string;
  data?: {
    transform?: (value) => any;
  };
}

export interface QueryRuleSet {
  condition?: 'AND' | 'OR' | 'NOT';
  rules?: Array<QueryRuleSet | QueryRule>;
}
