import {ButtonProps} from '../button/index';
import {Context, DynaContent} from '../dyna-content';
import {MenuItem} from '../menu/index';

export enum OverlayType {
  TABLE = 'table',
  CHART = 'chart',
  IMAGE = 'image',
  PANEL = 'panel',
  FRAME = 'frame',
  MENU = 'menu',
  TREE = 'tree',
  COMPONENT = 'component'
}

export interface Overlay {
  type?: OverlayType;
  icon?: string;
  content?: DynaContent | MenuItem[] | ButtonProps[] | any;
  context?: Context;
  styleClass?: string | ((ctx) => string);
  style?: string | {} | ((ctx) => string);
  overlayStyleClass?: string | ((ctx) => string);
  overlayStyle?: string | {} | ((ctx) => string);
  from?: string;
}

export interface OverlayContext {
  $event?: any;
  type?: OverlayType;
  icon?: string;
  dismissable?: boolean;
  showCloseIcon?: boolean;
  content?: DynaContent | MenuItem[] | ButtonProps[] | any;
  context?: Context;
  styleClass?: string | ((ctx) => string);
  style?: string | {} | ((ctx) => string);
  overlayStyleClass?: string | ((ctx) => string);
  overlayStyle?: string | {} | ((ctx) => string);
  id?: string;

  [key: string]: any;
}
