import {HttpHeaders, HttpParams} from '@angular/common/http';
import {ApiServerType} from '../api';

export interface HttpClientRequestOptions {
  body?: any;
  headers?: HttpHeaders;
  params?: HttpParams;
  observe?: 'body' | 'events' | 'response';
  reportProgress?: boolean;
  responseType?: HttpResponseType;
  withCredentials?: boolean;
}

export enum HttpMethod {
  GET = 'Get',
  POST = 'Post',
  DELETE = 'Delete',
  OPTIONS = 'Options',
  PUT = 'Put',
  HEAD = 'Head',
  PATCH = 'Patch'
}

export enum HttpResponseType {
  ARRAY = 'arraybuffer',
  BLOB = 'blob',
  JSON = 'json',
  TEXT = 'text',
}

export enum HttpContentType {
  FORM = 'application/x-www-form-urlencoded',
  MULTIPART = 'multipart/form-data',
  JSON = 'application/json',
  TEXT = 'text/plain',
}

export type HttpRequestPayload =
  string
  | { [key: string]: any }
  | Function;

export enum HttpRequestPayloadType {
  PARAMS = 'params',
  BODY = 'body'
}

export interface HttpRequestOptions {
  tag?: string | string[];
  uri?: string;
  serverType?: ApiServerType;
  method?: HttpMethod | string;
  headers?: { [name: string]: string | string[] };
  params?: HttpRequestPayload;
  body?: HttpRequestPayload;
  payload?: HttpRequestPayload;
  payloadType?: HttpRequestPayloadType;
  contentType?: HttpContentType;
  responseType?: HttpResponseType;
  observe?:  'body' | 'events' | 'response';
  reportProgress?: boolean;
  withCredentials?: boolean;

  retry?: number;

  dataPath?: string | string[];

  notifySuccess?: boolean;
  alertSuccess?: boolean;
  successMessage?: string;
  notifyFailure?: boolean;
  alertFailure?: boolean;
  failureMessage?: string;

  // store http response in state
  stateName?: string | string[];
  // keep context,used for 'this'
  context?: any;
  // http state
  skipRootHttpNotify?: boolean;

  // triggered on http request prepareï¼used to resolve or cancel request
  onRequest?: (options: HttpRequestOptions) => HttpRequestOptions | boolean;
  // triggered on http request successï¼used to resolve http success
  onResponse?: (response: any, request: HttpRequestOptions) => any;
  // triggered on http request errorï¼used to resolve http error
  onError?: (error: any, request: HttpRequestOptions) => any;
  // triggered on api request successï¼used to resolve api result
  onSuccess?: (resp: any, request: HttpRequestOptions) => any;
  // triggered on api request failureï¼used to resolve api failure
  onFailure?: (message: any, request: HttpRequestOptions) => any;

  [extended: string]: any;
}
