import {ApiDataProps} from '../api-data';
import {DataAttr} from '../data-attr';
import {DataItem, DataItemProps} from '../data-item';
import {DataType} from '../data-type';
import {DynaContent} from '../dyna-content';

export interface DataField {
  key?: string;
  label?: string;
  dataType?: DataType;
  dataAttr?: DataAttr;
  catCode?: string;
  length?: number;
  description?: DynaContent;
  order?: number;
  minLength?: number;
  maxLength?: number;
  max?: number;
  min?: number;
  pattern?: string | RegExp;
  defaultValue?: any;
  required?: boolean;
  placeholder?: string;
  disabled?: boolean;
  hidden?: boolean;
  dataItems?: DataItem[];
  validators?: {
    messageKey?: (control) => boolean;
  };
  apiDataProps?: ApiDataProps;
  dataItemProps?: DataItemProps;
  defaultKey?: string;
  fields?: {
    [key: string]: DataField;
  };
  aggKey?: string;
  sortKey?: string;
  parsers?: ((value: any) => {})[];
  formatter?: (value) => string;
}

export const FieldId = {
  key: 'id',
  label: 'id',
  dataType: DataType.STRING,
  dataAttr: DataAttr.ID
};
